import React from 'react';

export default () => (
  <style jsx>{`
    html {
        animation: crossfade 2s infinite alternate;
        background: #fff;
        font-family: sans-serif;
      }

        body {
        margin: 0;
      }

        svg {
        animation: fade 2s infinite alternate;
        display: block;
        fill: #000;
        height: 100vh;
        width: 100%;
      }

        @keyframes fade {
        0%,
        50% {
        fill: #000;
      }
        50.1%,
        100% {
        fill: #fff;
      }
      }

        @keyframes crossfade {
        0%,
        50% {
        background-color: #fff;
      }
        50.1%,
        100% {
        background-color: #000;
      }
    }
  `}</style>
);


